window.__va__(app => {
  const [cfg] = app.config.files.filter(
    file => file.name === 'mouseleave' && file.type === 'script'
  );

  if (!cfg || !cfg.options) {
    console.warn('mouseleave config is empty or missed options');
    return;
  }

  const isMac = navigator.platform.indexOf('Mac') > -1;

  /**
   * On the macbook the area is on the left, on windows - on the right.
   */
  const side = isMac ? 'left' : 'right';

  /**
   * Area in which the chat open code is triggered
   * with a message on specific platform.
   */
  const areas = {
    win: {
      x: 200,
      y: 50,
    },
    mac: {
      x: 150,
      y: 50,
    },
  };

  /**
   * Automatically sets to true to
   * not show the message every time.
   */
  let isDisabled = false;

  const getMousePos = e => ({
    x: e.pageX - document.body.scrollLeft,
    y: e.pageY - document.body.scrollTop,
  });

  const onMouseLeave = e => {
    const mousePos = getMousePos(e);

    const isLeaving =
      side === 'left'
        ? mousePos.x <= areas.mac.x && mousePos.y <= areas.mac.y
        : mousePos.x >= window.outerWidth - areas.win.x &&
          mousePos.y <= areas.win.y;

    if (isLeaving && !isDisabled) {
      isDisabled = true;

      if (typeof cfg.options.message === 'string') {
        app.emit('open');
        app.emit('textMessage', cfg.options.message);
      }
    }
  };

  app.on('closed', () => {
    document.addEventListener('mouseleave', onMouseLeave, false);
  });

  app.on('opened', () => {
    isDisabled = true;
    document.removeEventListener('mouseleave', onMouseLeave, false);
  });
});
